import React, {Component} from 'react';
import logo from '../../../style/images/logo.png';
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Card,
  Layout,
  Row,
  Col,
  Menu
} from 'antd';
import {headStyles, cardStyles, contentStyles, medusa, layoutStyles} from '../../../style/MainStyles.js';
import { connect } from 'react-redux';
import './local.css';


import {Link, Redirect} from "react-router-dom";
import { checkLoginOCI } from '../../../redux';
const {Header, Content} = Layout;
const FormItem = Form.Item;

class Entry extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  render() {
    return (
      <div>
        <Card title="Are you a Doctor or Pharmacist" bordered={false} style={{position: "absolute", left: "20vw", right: "20vw", top: "10vh", bottom: "70vh", backgroundColor: "#E8F1F5"}}>
              <p>Doctor</p>
              <p>Pharmacist</p>
        </Card>
      </div>
    );
  }
}


function mapDispatchToProps(dispatch) {
    return({
       checkloginoci: (e)=>{dispatch(checkLoginOCI(e))},
    })
}

function mapStateToProps(state) {
    return({
      loginreturn: state.loginreturn,
    })
}

export default (connect(
    mapStateToProps, mapDispatchToProps)(
    Entry
))



